import React from 'react'
import { Link } from 'react-router-dom';

class Nav extends React.Component{
	constructor(props)
	{
		super(props);		
	}

	render()
	{
		return(
			<div>
<nav class="navbar navbar-expand-lg navbar-dark ftco_navbar bg-dark ftco-navbar-light" id="ftco-navbar">
	    <div class="container">
	      <Link class="navbar-brand" to="/">Donkey<span>Cargo</span></Link>
	      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#ftco-nav" aria-controls="ftco-nav" aria-expanded="false" aria-label="Toggle navigation">
	        <span class="oi oi-menu"></span> Menu
	      </button>

	      <div class="collapse navbar-collapse" id="ftco-nav">
	        <ul class="navbar-nav ml-auto">
	          <li class={this.props.active==='home'?"nav-item active":"nav-item"}><Link to="/" class="nav-link">Home</Link></li>
	          <li class={this.props.active==='about'?"nav-item active":"nav-item"}><Link to="/About" class="nav-link">About</Link></li>
	          <li class={this.props.active==='contact'?"nav-item active":"nav-item"}><Link to="/Contact" class="nav-link">Contact</Link></li>
	        </ul>
	      </div>
	    </div>
	  </nav>
	  </div>
			)
	}
}

export default Nav;